@charset "utf-8";
@import "lib/reset.min.css";
@import "_vars.sass";

.is-pc {
  display: none; }

body {
  color: $black;
  background: $white;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  font-size: 14px;
  line-height: 2.0;
  @include ff_TPSky_R; }

a {
  color: $cyan; }

li {
  list-style: none; }


header {
  position: relative;
  padding: 30px;
  h1 {
    img {
      height: 35px; } }
  nav {
    li {
      font-size: 10px;
      line-height: 1.0px;
      display: inline-block;
      margin-right: 10px;
      @include ff_TPSky_B;
      span {
        opacity: 0.3; }
      a {
        color: $pink; } } } }

footer {
  background: $pink;
  position: relative;
  padding: 30px;
  p.address {
    display: inline-block;
    color: $white;
    font-size: 8px;
    white-space: nowrap;
    &.en {
      margin-top: 20px; }
    span {
      @include ff_TPSky_B; } }
  p.logo {
      position: absolute;
      top: 40px;
      right: 40px;
      img {
        height: 25px; } } }

body.index {
  .kv {
    min-height: 100vw;
    position: relative;
    background: url("/assets/img/top/kv.jpg") no-repeat center;
    background-size: cover;
    .inner {
      background: rgba(0,0,0,0.5) url("/assets/img/top/bg.png") no-repeat top right;
      background-size: contain;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      .catch {
        @include ff_TPSky_L;
        font-size: 20px;
        padding-top: 30px;
        padding-left: 30px;
        color: $white; }
      .copy {
        line-height: 1.0;
        font-size: 10px;
        color: $white;
        position: absolute;
        bottom: 30px;
        right: 30px; } } } }


body.about {
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 281px 400px; }

  #container {
    padding: 30px;

    h2 {
      text-align: center;
      line-height: 1.0;
      font-size: 28px;
      @include ff_TPSky_L;
      margin-bottom: 60px;
      position: relative;
      &:before {
        position: absolute;
        content: '';
        top: -30px;
        left: 50%;
        width: 30px;
        height: 1px;
        background: $pink;
        margin-left: -20px; }
      span {
        @include ff_TPSky_R;
        display: block;
        font-size: 12px;
        letter-spacing: 0.2em;
        margin-bottom: 20px; } }

    .vision {
      margin-bottom: 90px;
      h2 {
        img {
          width: 302px; }
        &:before {
          display: none; } }
      .culum-wraper {
        .culum {
          p {
            margin-top: -7px;
            margin-bottom: 2em;
            &:last-child {
              margin-bottom: 0em; } } } } }

    .lineup {
      h2 {
        img {
          width: 112px; } }
      margin-bottom: 90px;
      ul {
        padding-left: 2em;
        li {
          @include ff_TPSky_L;
          font-size: 24px;
          list-style: disc;
          margin-top: -14px;
          margin-bottom: 30px;
          &:last-child {
            margin-bottom: 0px; } } } }

    .access {
      h2 {
        img {
          width: 112px; } }
      p {
        margin-top: -7px;
        margin-bottom: 30px;
        span {
          @include ff_TPSky_B; } }
      .map {
        width: 100%;
        height: 300px;
        background: $gray; } } } }

body.member {
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 281px 400px; }

  #container {
    padding: 30px 30px 30px 0px;
    .member {
        margin: 0 auto;
        vertical-align: top;
        margin-bottom: 90px;
        position: relative;
        &:before {
          position: absolute;
          content: '';
          top: -30px;
          left: 30px;
          width: 30px;
          height: 1px;
          background: $pink; }
        &:last-child {
          margin-bottom: 0px; }
        &:first-child {
            &:before {
              display: none; } }
        .img {
          vertical-align: top;
          display: inline-block;
          width: 100px;
          padding-left: 30px;
          margin-right: 30px;
          background: rgba(255,0,110,0.2);
          img {
            width: 100%; } }
        .name {
          display: inline-block;
          margin-right: 40px;
          h2 {
            font-size: 24px;
            line-height: 1.0;
            @include ff_TPSky_L; }
          .en {
            font-size: 10px;
            line-height: 1.0;
            letter-spacing: 0.2em;
            margin-top: 15px; }
          .position {
            font-size: 10px;
            margin-top: 15px;
            margin-bottom: -5px; } }
        .desc {
          padding-left: 30px;
          margin-top: 16px; } } } }

body.contact {
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 281px 400px; }

  #container {
    padding: 30px;
    .lead {
      margin-bottom: 60px;
      font-size: 14px;
      @include ff_TPSky_L; }
    .form {
      position: relative;
      padding-top: 30px;
      &:before {
        position: absolute;
        content: '';
        top: 0px;
        left: 0px;
        width: 30px;
        height: 1px;
        background: $pink; }
      div {
        margin-bottom: 30px;
        p {
          font-size: 10px;
          letter-spacing: 0.2em;
          margin-bottom: 10px; } }
      input[type="text"] {
        color: $black;
        font-size: 14px;
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        outline: none;
        border: 1px solid rgba(0,0,0,0.3);
        border-radius: 4px;
        &:focus {
          border: 1px solid $cyan; }
        &.error {
          border: 1px solid $pink; }
        &:placeholder-shown {
          color: #E0E0E0; }
        &::-webkit-input-placeholder {
          color: #E0E0E0; } }

      input[type="radio"] {
        display: none; }

      input[type="radio"]:checked + label::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 7px;
        width: 18px;
        height: 18px;
        margin-top: -8px;
        background: #3498db;
        border-radius: 100%; }

      label {
        color: $black;
        line-height: 1.0;
        margin-bottom: 15px;
        cursor: pointer;
        position: relative;
        display: block;
        padding: 7px 7px 7px 50px;
        font-size: 10px;
        line-height: 1.5;
        span {
          @include ff_TPSky_B;
          font-size: 14px;
          font-weight: bold; }
        &:before {
          position: absolute;
          content: '';
          top: 50%;
          left: 0;
          width: 30px;
          height: 30px;
          margin-top: -15px;
          background: #fff;
          border: 1px solid #ccc;
          border-radius: 100%; } }

      textarea {
        color: $black;
        font-size: 14px;
        width: 100%;
        height: 100px;
        padding: 10px;
        box-sizing: border-box;
        outline: none;
        border: 1px solid rgba(0,0,0,0.3);
        border-radius: 4px;
        &.error {
          border: 1px solid $pink; }
        &:focus {
          border: 1px solid $cyan; }
        &:placeholder-shown {
          color: #E0E0E0; }
        &::-webkit-input-placeholder {
          color: #E0E0E0; } }

      a.submit {
        display: block;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        color: $white;
        box-sizing: border-box;
        font-size: 16px;
        width: 100%;
        padding: 20px;
        border-radius: 4px;
        background: $cyan; } } } }

body.topics {
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 281px 400px; }

  #container {
    padding: 30px;
    .posts {
      .post {
        position: relative;
        margin-top: -16px;
        margin-bottom: 90px;
        &:before {
          position: absolute;
          content: '';
          top: -30px;
          left: 0px;
          width: 30px;
          height: 1px;
          background: $pink; }
        &:first-child {
          &:before {
            display: none; } }
        .date {
          @include ff_TPSky_B;
          line-height: 1.0;
          margin-bottom: 20px; }
        .post-body {
          h2 {
            font-size: 24px;
            @include ff_TPSky_L;
            margin-bottom: 50px;
            margin-top: -12px; }
          .text {
            margin-bottom: -7px;
            margin-top: -7px; }
          p {
            margin-top: -7px;
            margin-bottom: 30px; }
          figure {
            margin-left: -30px;
            margin-right: -30px;
            background: rgba(0,0,0,0.05);
            padding: 10px;
            text-align: center;
            margin-bottom: 30px;
            img {
              max-width: 100%; } }
          .foot {
            margin-top: 50px;
            font-size: 10px;
            @include ff_TPSky_B;
            line-height: 1.0;
            position: relative;
            .links {
              position: absolute;
              top: 0px;
              right: 0px;
              li {
                display: inline-block;
                margin-left: 20px; } } } } } } } }
